h1{
    font-size: 100px;
    font-weight: 800;
    text-align: center;
    margin-top: 50px;    
    padding: 15px;
    color: white;
    letter-spacing: 10px;    
    border : 10px solid #fff;
    border-radius: 100px;
    text-shadow: 0 0 50px skyblue;
    box-shadow: 0px 0px 100px skyblue;
    -webkit-animation: animate 10s ease-in-out infinite;
            animation: animate 10s ease-in-out infinite;
}
@-webkit-keyframes animate {
    0%{box-shadow: 0 0 100px white;}    
    20%{box-shadow: 0 0 200px blue;}
    40%{box-shadow: 0 0 200px hotpink;}
    60%{box-shadow: 0 0 200px skyblue;}
    80%{box-shadow: 0 0 100px yellow;}
    100%{box-shadow: 0 0 100px white;}
}
@keyframes animate {
    0%{box-shadow: 0 0 100px white;}    
    20%{box-shadow: 0 0 200px blue;}
    40%{box-shadow: 0 0 200px hotpink;}
    60%{box-shadow: 0 0 200px skyblue;}
    80%{box-shadow: 0 0 100px yellow;}
    100%{box-shadow: 0 0 100px white;}
}
